@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
*{
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    background-color: #F5F6FA;
}
.app-header{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding: 15px;
    color:#F67602;
    font-weight: 600;
}
.app-country-list> h2{
    text-align: center;
    margin-bottom: 15px;
    color: #FA5B87;
    font-weight: 900;
}
.MuiSvgIcon-root{
    -webkit-animation: rotation 5s infinite linear;
            animation: rotation 5s infinite linear;
}
@-webkit-keyframes rotation {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(359deg);
              transform: rotate(359deg);
    }
  }
@keyframes rotation {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(359deg);
              transform: rotate(359deg);
    }
  }
.map{
    height: 500px;
    margin: 30px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 50px;
    box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
}
.map .leaflet-container{
    height: 100%;
}

@media screen and (max-width: 800px) {
    .map{
        height: 300px;
    }
}


.countrylist{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.list{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-evenly;
    align-items: center;
    max-height: 480px;
    max-width: 980px;
    overflow-y: scroll;
}
.country-refresh{
    margin: 20px;
    padding: 50px;
    text-align: center;
}
.countryCard{
    margin: 0;
    padding: 0;
}
.countryCard-details{
    width: 220px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 10px;
    border-radius: 20px;
    background-color :#e7eff9;
    background-image :linear-gradient(315deg, #e7eff9 0%, #cfd6e6 74%);        
    box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
    cursor: pointer;
    transition: 200ms ease-in-out;
}
.countryCard-details:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.countryCard-details > img{
    position: relative;
    width: 200px;
    object-fit: contain;
    max-height: 200px;

}
.countryCard-details > p{
    margin-top: 15px;
    font-weight: 600;
}
.countryCard-detail{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content:space-around;
}
.countryCard-left > img{
    width: 200px;
    border-radius: 20px;
}
.countryCard-left > p,li{
    font-weight: 600;
}
.countryCard-right > p,li{
    font-weight: 600;
}
.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0;
    background-color: #ebf928;
    background-image: linear-gradient(315deg, #ebf928 0%, #d425b5 74%);
    font-family: 'Righteous', cursive;
}
